@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Sansita+Swashed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Sansita+Swashed&family=Share+Tech&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0;padding:0;box-sizing:border-box}footer{position:fixed;bottom:2rem;right:2rem;font-size:2rem;font-family:'Indie Flower', cursive}body{background:linear-gradient(#EFF147, #3FB869) no-repeat center fixed;background-size:cover}

.display-container{display:flex;background:black;width:320px;height:100px;justify-content:center;padding:1rem .8rem;margin:0}.display-container input{background:#9FAFAF;font-size:25px;border-radius:10px;padding:5%;font-family:'VT323', monospace;outline:none}

.btn-container{border:0.5px solid black;background:#29B5F6;font-size:1rem;font-weight:bold;height:80px;outline:none;width:80px;align-items:center;color:#fff}.btn-container:hover{border:1px solid #a9e4ff}.btn-container:active{background:#a9e4ff}

.keyboard-container{margin:0}.row{display:flex}

.cal-app{position:relative;top:8rem;left:calc(50% - 160px);width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;text-align:center}.cal-section .calculator-area{border:4px solid gray;border-radius:5px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.title{margin-bottom:1rem;font-family:'Share Tech', sans-serif}

